$playfair-display-font-path: "./files" !default;

/* playfair-display-400normal - latin */
@font-face {
  font-family: 'Playfair Display';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Playfair Display Regular '),
    local('Playfair Display-Regular'),
    url('#{$playfair-display-font-path}/playfair-display-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('#{$playfair-display-font-path}/playfair-display-latin-400.woff') format('woff'); /* Modern Browsers */
}

/* playfair-display-400italic - latin */
@font-face {
  font-family: 'Playfair Display';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src:
    local('Playfair Display Regular italic'),
    local('Playfair Display-Regularitalic'),
    url('#{$playfair-display-font-path}/playfair-display-latin-400italic.woff2') format('woff2'), /* Super Modern Browsers */
    url('#{$playfair-display-font-path}/playfair-display-latin-400italic.woff') format('woff'); /* Modern Browsers */
}

